.block-dealer {
  background-image: url(../../img/bg/dealer.jpg);
  display: flex;
  flex-flow: column;
  align-items: center;
}

.block-dealer .block__title {
  text-align: center;
  margin-bottom: 45px;
}

.block-dealer .block__content {
  max-width: 485px;
  margin-bottom: 30px;
}

.block-dealer .block__content p {
  margin: 0;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
}

.block-dealer .block__link {
  display: block;
}